const defaultState = {
    todos: [
        {id: 30, title: "  ", type: "1", date: "2023-03-27"},
        {id: 60, title: '  ', type: "3", date: "2023-03-27"},
        {id: 80, title: '   ', type: "4", date: "2023-03-27"},
        {id: 90, title: '  ', type: "4", date: "2023-03-27"},
    ],
    success: [
        {id: 10, title: " ", type: "3",date: "2023-03-27", day: ""},
        {id: 200, title: '  "  "', type: "3", date: "2023-03-27", day: ""},
        {id: 70, title: '  ', type: "3", date: "2023-03-27", day: ""},
        {id: 40, title: '  " "', type: "3", date: "2023-03-27", day: ""},
        {id: 50, title: '  " "', type: "3", date: "2023-03-27", day: ""},
        {id: 100, title: ' ', type: "1", date: "2023-03-27", day: ""},
    ],
    remove: 3
}

export const todosReducer = (state = defaultState, action) => {
    switch (action.type){
        case "ADD_TODO":
            return {...state, todos: [...state.todos, action.payload]}
        case "REMOVE_TODO":
            const newArray = state.todos.filter(old => old.id !== action.payload)
            return{...state, todos: [...newArray], remove: state.remove + 1}
        case "SUCCESS_TODO":
            const newArrayTodo = state.todos.filter(old => old.id !== action.payload[0].id)
            return{...state, success: [...state.success, ...action.payload], todos: [...newArrayTodo]}
        case "REMOVE_SUCCESS_TODO":
            const newSuccessArray = state.success.filter(old => old.id !== action.payload)
            return{...state, success: [...newSuccessArray], remove: state.remove + 1}
        default: 
            return state
    }
} 